/* eslint-disable */
import loadjs from 'loadjs';



/**
 * body encode
 * 
 *
 * @export
 * @returns
 */
export function requestEncode(param) {
  return 'jsondata=' + encodeURI(JSON.stringify(param));
}
/**
 * spreadjs
 *
 * @export
 * @param {any} callback
 */
export function loadGcSpread(callback) {
  if (typeof GcSpread == 'undefined') {
    loadjs(`${env.host}assets/js/spread/gcspread.sheets.all.9.40.20153.0.min.js`, { success: callback });
    console.info('spreadjs is ready');
  } else {
    callback();
    console.info('spreadjs is ready');
  }
}

export function rand(min, max) {
  // 
  let num = Math.random();
  let newNum = Number(num.toFixed(2));
  let floor = Math.floor(Math.random() * (max - min)) + min;
  let data = floor + newNum;
  return Number(data.toFixed(2));
}

export function integer(min, max) {
  // 
  return Math.floor(Math.random() * (max - min)) + min;
}

export function randPercent(min, max) {
  // 
  return Math.floor(Math.random() * (max - min)) + min + '%';
}



